        //TIRAR BARRA EIXO X
        overflow-x: hidden;
    }

    .section-main {
        background: $colorBaseGradient;

        position: relative;
        width: 100vw;
        height: 100vh;
        //justify-content: center;
        //align-items: center;
        display: block;
        //flex-direction: row;
        //grid-gap: 10%;
