@import "../../Components/settings";

@import "../../colors/colors";


@media (min-width:320px) and (max-width: 899px) {

  .main-login {
    width: 100%;
    background: $colorBaseGradient;
    *{
      background-color: rgb(255, 255, 255);
    }
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .registration__system {
    background: $colorBaseGradient;
    width: 350px;
    margin: 0 auto;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  
    &__login {
      width: 100%;
      border: 1px solid rgb(219, 219, 219);
      border-radius: 1px;
      display: flex;
      margin: 0 0 10px;
      padding: 10px 0;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      text-align: center;
  
      img {
        margin-top: calc(4px * 9);
        margin-bottom: calc(4px * 3);
        width: 175px;
      }
  
      form {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
  
        input {
          outline: none;
          padding: 9px 0 7px 8px;
          width: 100%;
          background-color: rgb(250, 250, 250);
          margin: 0 40px 6px;
          border: 1px solid rgb(219, 219, 219);
          color: #000;
        }
  
        button {
          margin: 8px 40px;
          background-color: rgba(0, 149, 246);
          color: #fff;
          border: 0;
          border-radius: 8px;
          width: 100%;
          padding: 7px 16px;
          //cursor: pointer;
  
        }
      }
  
      &__otheror {
        display: flex;
        width: 100%;
        align-items: center;
        justify-content: center;
  
        p {
          margin: 0 18px;
          flex-basis: 10%;
        }
  
        .otheror__line {
          flex-basis: 25%;
          height: 1px;
          background-color: rgb(219, 219, 219);
        }
      }
  
      &__endpassword {
        a {
          color: rgb(0, 55, 107);
          font-size: 12px;
          line-height: 12px;
          margin-top: 12px !important;
          text-align: center !important;
        }
      }
    }
    &__orregister {
      width: 100%;
      border: 1px solid rgb(219, 219, 219);
      border-radius: 1px;
      display: flex;
      margin: 0 0 10px;
      padding: 10px 0;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      text-align: center;
      span {
        //margin: 15px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        color: rgb(38, 38, 38);
        font-size: 14px;
        margin: 15px;
        text-align: center;
        a {
          color: rgb(0, 149, 246);
          margin-left: 10px;
        }
      }
    }
  }


}

